import styles from './order-details.module.css';
import doneSvg from '../../images/done.png';
import classNames from "classnames";
import {FC} from "react";

type TOrderDetails = {
    isLoading: boolean,
    isOk: boolean,
    orderId: number | null
}

const OrderDetails: FC<TOrderDetails> = ({isLoading, isOk, orderId}) => {
    return (
        <>
            {isLoading &&
                <div className={styles.textCenter}>
                    <div className="text text_type_main-medium">
                        ...
                    </div>
                </div>}

            {!isLoading && isOk &&
                (
                    <>
                        <span
                            className={classNames(styles.orderIdentifier, "text text_type_digits-large")}
                        >{orderId}</span>
                        <span className="pt-6 text text_type_main-medium"> </span>
                        <img className={styles.imgOk} src={doneSvg} alt="done"/>
                        <span className="text text_type_main-small">   </span>
                        <span className="text text_type_main-small text_color_inactive">
                                
                        </span>
                    </>)}
            {!isLoading && !isOk &&
                <div className={styles.textCenter}>
                    <div className="text text_type_main-medium">
                            ,   
                    </div>
                </div>}
        </>
    );
}

export default OrderDetails;
